.message {
    background: url(../img/bg.png) repeat 0 0;
    margin: 100px 0;
    padding: 160px 0;
    &__content{
        display: flex;
        align-items: center;
        justify-content: space-between;
        &_reverse{
            flex-direction: row-reverse;
        }
    }
    &__img {
        width: 400px;
    }
    &__info {
        width: 700px;
    }
    &__article {
        margin-bottom: 60px;
    }
    &__text {
        font-size: 20px;
        color: $text-color;
    }
}


